import { Injectable, EventEmitter } from '@angular/core';
import { isPresent } from './util';

/**
 * @hidden
 */
export interface ISelectionEvent {
    indices: number[];
}

/**
 * @hidden
 */
@Injectable()
export class SelectionService {
    public onSelect: EventEmitter<ISelectionEvent> = new EventEmitter<ISelectionEvent>();
    public onChange: EventEmitter<ISelectionEvent> = new EventEmitter<ISelectionEvent>();
    public onFocus: EventEmitter<number> = new EventEmitter<number>();
    private selectedIndices: number[] = [];
    private focusedIndex: number;

    public isSelected(index: number): boolean {
        return isPresent(this.selectedIndices.find(current => current === index));
    }

    public isFocused(index: number): boolean {
        return index === this.focused;
    }

    public select(index: number): void {
        if (this.isSelected(index)) { return; }

        this.selectedIndices = [index];
        this.focused = index;
        this.onSelect.emit({ indices: [index] });
    }

    public change(index: number): void {
        this.selectedIndices = [index];
        this.focused = index;
        this.onChange.emit({ indices: [index] });
    }

    public resetSelection(index: number | number[]): void {
        this.selectedIndices = index instanceof Array ? index : [index];
        this.focused = this.selectedIndices[this.selectedIndices.length - 1];
    }

    public get selected(): number[] {
        return this.selectedIndices;
    }

    public get focused(): number {
        return this.focusedIndex;
    }

    public set focused(index: number) {
        this.focusedIndex = index;
        this.onFocus.emit(index);
    }
}



